import { z } from 'zod';
import type { Prisma } from '@prisma/client-cms_admin';

/////////////////////////////////////////
// HELPER FUNCTIONS
/////////////////////////////////////////


/////////////////////////////////////////
// ENUMS
/////////////////////////////////////////

export const AuditsScalarFieldEnumSchema = z.enum(['id','createdAt','auditId','auditType','userId','username','action','auditChanges','version']);

export const ProductLineScalarFieldEnumSchema = z.enum(['id','createdAt','updatedAt','isDeleted','name','description']);

export const SortOrderSchema = z.enum(['asc','desc']);

export const TransactionIsolationLevelSchema = z.enum(['ReadUncommitted','ReadCommitted','RepeatableRead','Serializable']);
/////////////////////////////////////////
// MODELS
/////////////////////////////////////////

/////////////////////////////////////////
// PRODUCT LINE SCHEMA
/////////////////////////////////////////

export const ProductLineSchema = z.object({
  id: z.number().int(),
  createdAt: z.date(),
  updatedAt: z.date(),
  isDeleted: z.boolean(),
  name: z.string().openapi({"title":""}),
  description: z.string().openapi({"title":"","column_type":"textarea"}),
}).openapi({"primary_key":"id","display_name":"","display_column":"name","display_primary_key":"false","searchable_columns":"name"})

export type ProductLine = z.infer<typeof ProductLineSchema>

/////////////////////////////////////////
// AUDITS SCHEMA
/////////////////////////////////////////

export const AuditsSchema = z.object({
  id: z.number().int(),
  createdAt: z.date(),
  auditId: z.number().int().openapi({"title":""}),
  auditType: z.string().openapi({"title":"("}),
  userId: z.string().openapi({"title":""}),
  username: z.string().nullable().openapi({"title":""}),
  action: z.string().openapi({"title":"(e.g."}),
  auditChanges: z.string().openapi({"title":""}),
  version: z.number().int().openapi({"title":""}),
}).openapi({"display_name":""})

export type Audits = z.infer<typeof AuditsSchema>
